import React, { useState } from 'react';
import { MapContainer,  GeoJSON, ScaleControl } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet-fullscreen/dist/Leaflet.fullscreen.js';
import 'leaflet-fullscreen/dist/leaflet.fullscreen.css';
import * as L from 'leaflet';
import ColorPalette from '../components/ColorPalette';
import chroma from 'chroma-js';

const VisualizeMap = ({ minValueMap, setMinValueMap, maxValueMap, setMaxValueMap, showVisualizeMap, mapData, finalData, 
  selectedPaletteColors, setSelectedPaletteColors,
  selectedPaletteName,
  setSelectedPaletteName,
  handleNext, handleBack,
  nullColor, setNullColor,
  mapCenter,mapZoom
}) => {
  const [selectedFeature, setSelectedFeature] = useState([]);


  const getColor = (value) => {
    if (finalData.length > 0) {
      const nonEmptyValues = finalData.filter((data) => data.Value !== "");
      setMinValueMap(Math.min(...nonEmptyValues.map((data) => parseInt(data.Value))))
      setMaxValueMap(Math.max(...nonEmptyValues.map((data) => parseInt(data.Value))))
    }
    if (!selectedPaletteColors || selectedPaletteColors.length < 2) {
      return '#C0C0C0';
    }

    const scale = chroma.scale(selectedPaletteColors).domain([minValueMap, maxValueMap]);
    return scale(value).hex();
  };



  const style = (feature) => {
    const name = feature.properties.Name;
    const value = finalData.find((data) => data.Name === name)?.Value || null;

    return {
      fillColor: value ? getColor(value) : nullColor,
      opacity: 0.5,
      weight: selectedFeature.name === name ? 2 : 1,
      color: 'black',
      fillOpacity: selectedFeature.name === name ? 1 : 0.7,
      dashArray: '2',
    };
  };

  function getVoivodeshipName(feature, layer) {
    const name = layer.feature.properties.Name;
    const value = finalData.find((data) => data.Name === name)?.Value || '';
    const popupContent = `
      <div>
        ${name}<br/>
        ${value}
      </div>
    `;
    layer.bindPopup(popupContent);
  }

  function setWindowWidth() {
    var viewportWidth = window.innerWidth;
    var windowWidth;
    if (viewportWidth <= [767]) {
      windowWidth = { width: '100%', height: "450px", backgroundColor: 'white', border: 'none', margin: 'auto' };
    } else {
      windowWidth = { width: '100%', height: "600px", backgroundColor: 'white', border: 'none', margin: 'auto' };
    }
    return windowWidth;
  }



  function onEachFeature(feature, layer) {
    layer.on('mouseover', function (e) {
      const layer = e.target;
      const name = layer.feature.properties.Name;
      const value = finalData.find((data) => data.Name === name)?.Value || '-';
      setSelectedFeature({ name, value });
      getVoivodeshipName(feature, layer);
      this.openPopup();
      layer.setStyle(style(layer.feature));
      layer.bringToFront();
    });

    layer.on('mouseout', function (e) {
      setSelectedFeature({});
      this.closePopup();
      const layer = e.target;
      layer.setStyle(style(layer.feature));
    });
  }




  const handleNullColorChange = (event) => {
    setNullColor(event.target.value);
  };





  return (
    <div>
      <div className="map_visualize_container">
        <div className="row ">

          {mapData ? (

            <>
              <div className="col-md-3">
                <ColorPalette
                  setSelectedPaletteColors={setSelectedPaletteColors}
                  selectedPaletteColors={selectedPaletteColors}
                  minValueMap={minValueMap}
                  maxValueMap={maxValueMap}
                  selectedPaletteName={selectedPaletteName}
                  setSelectedPaletteName={setSelectedPaletteName}
                />

                <div className="dropdown">
                  <button
                    className="color_ramp_btn dropdown-toggle"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    Select Color for null values
                  </button>
                  <div className="dropdown-menu null_color">
                    <input type="color" name="null_color" value={nullColor} onChange={handleNullColorChange} />
                  </div>
                </div>

              </div>
              <div className="col-md-9">
                <div className="map_visualize">
                  {showVisualizeMap ? (
                     <MapContainer
                     fullscreenControl={true}
                     center={mapCenter}
                     zoom={mapZoom}
                     minZoom={-1}
                       
                       style={setWindowWidth()}
                       attributionControl={false}
                       zoomDelta={0.25}
                       zoomSnap={0}
                       crs={L.CRS.EPSG4326}
                       
                       scrollWheelZoom={false}
                       dragging={false}
                     >   
   
                       <GeoJSON
                         data={mapData.features}
                         style={style}
                         onEachFeature={onEachFeature}
                       />
   
                       
   
                       <ScaleControl />
                     </MapContainer>

                  ):(
                    <div className='map_loader_container'>
                      <div className="preloader">
                                    <div className="loader"></div>
                                </div>

                    </div>
                  )}

                 

                </div>
              </div>
            </>

          ) : (
            <div className='warning_container'>Please Select a map to Visualize.</div>
          )}

        </div>

      </div>
      <div className="page_change_btn">
        <button onClick={handleBack}> &laquo; Back </button>
        <button onClick={handleNext}>Proceed &raquo;</button>
      </div>
    </div>
  );
};

export default VisualizeMap;
