import React, { useState, useEffect, useRef } from 'react';
import { MapContainer, useMap, GeoJSON, FeatureGroup, ScaleControl } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import * as L from 'leaflet';
import northarrow from "../assets/north_arrow.png"
import chroma from 'chroma-js';
import domtoimage from 'dom-to-image';
import jsPDF from 'jspdf';

const ExportMap = ({ mapData, finalData, selectedPaletteColors, showExportMap, setShowExportMap, minValueMap, maxValueMap, handleBack, nullColor }) => {
  const [mapTitle, setMapTitle] = useState('');
  const [mapSummary, setMapSummary] = useState('');
  const [mapCreator, setMapCreator] = useState('');
  const [mapLegend, setMapLegend] = useState('');

  const mapContainerRef = useRef(null);

  function setWindowWidth() {
    var viewportWidth = window.innerWidth;
    var windowWidth;
    if (viewportWidth <= [767]) {
      windowWidth = { width: '100%', height: "450px", backgroundColor: 'white', border: 'none', margin: 'auto' };
    } else {
      windowWidth = { width: '100%', height: "600px", backgroundColor: 'white', border: 'none', margin: 'auto' };
    }
    return windowWidth;
  }


  const getColor = (value) => {
    if (!selectedPaletteColors || selectedPaletteColors.length < 2) {
      return '#C0C0C0';
    }
    const scale = chroma.scale(selectedPaletteColors).domain([minValueMap, maxValueMap]);
    return scale(value).hex();
  };
  const style = (feature) => {
    const name = feature.properties.Name;
    const value = finalData.find((data) => data.Name === name)?.Value || null;

    return {
      fillColor: value ? getColor(value) : nullColor,
      opacity: 0.5,
      weight: 1.5,
      color: 'black',
      fillOpacity: 1,
    };
  };

  const FitBoundsExample = () => {
    const map = useMap();
    useEffect(() => {
      if (mapData) {
        const geoJSONLayer = L.geoJSON(mapData);
        const geoJSONBounds = geoJSONLayer.getBounds();
        map.fitBounds(geoJSONBounds);
        map.setMaxBounds(geoJSONBounds);
      }
    }, [mapData]);

    return null;
  };


  const generateGradientStyle = (colors) => {
    if (colors && colors.length >= 2) {
      const gradient = `linear-gradient(to right, ${colors.join(', ')})`;
      return {
        backgroundImage: gradient,
      };
    }
    return null;
  };




  const handleExportMapImage = () => {
    const mapContainer = mapContainerRef.current;

    const options = {
      width: mapContainer.offsetWidth * 4, // Adjust the width based on the desired DPI
      height: mapContainer.offsetHeight * 4, // Adjust the height based on the desired DPI
      style: {
        transform: 'scale(4)', // Adjust the scale value based on the desired DPI
        transformOrigin: 'top left',
      },
    };

    domtoimage
      .toBlob(mapContainer, options)
      .then((blob) => {
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = 'map.png';
        link.click();
        URL.revokeObjectURL(link.href);
      })
      .catch((error) => {
        console.error('Error exporting map image:', error);
      });
  };



  const handleExportMapPDF = () => {
    const mapContainer = mapContainerRef.current;
    const viewportWidth = window.innerWidth;

    const options = {
      width: mapContainer.offsetWidth * 2,
      height: mapContainer.offsetHeight * 2,
      style: {
        transform: 'scale(2)',
        transformOrigin: 'top left',
        width: '100%',
        height: 'auto',
      },
    };

    domtoimage
      .toBlob(mapContainer, options)
      .then((blob) => {
        const reader = new FileReader();
        reader.onloadend = () => {
          const img = new Image();
          img.onload = () => {
            const aspectRatio = img.width / img.height;
            const pdf = new jsPDF('portrait', 'px', 'a4');

            if (viewportWidth <= 767) {
              const pdfHeight = pdf.internal.pageSize.getHeight() - 20; // Set the desired height for PDF if viewport width <= 767
              const pdfWidth = pdfHeight * aspectRatio;
              const x = (pdf.internal.pageSize.getWidth() - pdfWidth) / 2;
              const y = (pdf.internal.pageSize.getHeight() - pdfHeight) / 2;
              pdf.addImage(img, 'PNG', x, y, pdfWidth, pdfHeight);
            } else {
              const pdfWidth = pdf.internal.pageSize.getWidth(); // Use A4 width for PDF if viewport width > 767
              const pdfHeight = pdfWidth / aspectRatio;
              const x = 0;
              const y = (pdf.internal.pageSize.getHeight() - pdfHeight) / 2;
              pdf.addImage(img, 'PNG', x, y, pdfWidth, pdfHeight);
            }


            // Add text line at the bottom
            const websiteName = 'This map has been downloaded from GEOSPATIAL SMART THINKERS';
            const fontSize = 10;

            const textWidth = pdf.getStringUnitWidth(websiteName) * fontSize / pdf.internal.scaleFactor;
            const textX = (pdf.internal.pageSize.getWidth() - textWidth) / 2; // X-coordinate for center alignment
            const textY = pdf.internal.pageSize.getHeight() - 10; // Y-coordinate for the text

            pdf.setFontSize(fontSize);
            pdf.text(textX, textY, websiteName);


            const pdfOptions = {
              compress: true, // Enable compression
              quality: 0.4, // Adjust the quality (0.0 - 1.0, default is 0.9)
            };

            pdf.save('map.pdf', pdfOptions);
          };
          img.src = reader.result;
        };
        reader.readAsDataURL(blob);
      })
      .catch((error) => {
        console.error('Error exporting map image:', error);
      });
  };



  return (
    <div>

      {mapData ? (
        <div className="row">
          <div className="col-md-3">
            <div className="inser_title_container">
              <input
                type='text'
                placeholder='Insert the map title here'
                value={mapTitle}
                onChange={(e) => setMapTitle(e.target.value)}
              />
              <textarea
                rows={4}
                placeholder='Summary of the Map.'
                value={mapSummary}
                onChange={(e) => setMapSummary(e.target.value)} />

              <input
                type='text'
                placeholder='Legend Name'
                value={mapLegend}
                onChange={(e) => setMapLegend(e.target.value)}
              />
              <input
                type='text'
                placeholder='Created by'
                title='Here you can add your name to show on the map'
                value={mapCreator}
                onChange={(e) => setMapCreator(e.target.value)}
              />

            </div>

            <button onClick={handleExportMapImage} className='export_btn'>Export as Image</button>
            <button onClick={handleExportMapPDF} className='export_btn'>Export as PDF</button>


          </div>
          <div className="col-md-9">

            <div className="export_map" ref={mapContainerRef}>
              <div className="export_map_inner">
                <p className='map_title'>{mapTitle}</p>
                <div className="north_arrow_img">
                  <img src={northarrow} alt='North Arrow' />

                </div>

                {showExportMap ? (
                  <MapContainer
                    style={setWindowWidth()}
                    attributionControl={false}
                    scrollWheelZoom={false}
                    zoomControl={false}
                    doubleClickZoom={false}
                    crs={L.CRS.EPSG4326}
                    zoomDelta={0.25}
                    zoomSnap={0}
                    minZoom={-1}


                    dragging={false}
                    keyboard={false}
                    touchZoom={false}
                    boxZoom={false}
                    tap={false}
                  >
                    <FitBoundsExample />
                    <FeatureGroup>
                      <GeoJSON
                        data={mapData.features}
                        style={style}
                        interactive={false}
                      />
                    </FeatureGroup>
                    <ScaleControl />
                  </MapContainer>

                ) : (
                  <div className='map_loader_container'>
                    <div className="preloader">
                      <span className="loader"></span>
                    </div>

                  </div>
                )}



                <div className="row">
                  <div className="col-md-6">
                    {mapSummary && (
                      <p className='map_summary'><strong>Map Summary: </strong>{mapSummary}</p>
                    )}

                  </div>
                  <div className="col-md-6">
                    <div className='export_map_legend_container'>
                      <div className="export_map_legend">
                        {selectedPaletteColors.length > 0 && mapLegend && (
                          <>
                            <p className='map_summary'>{mapLegend}</p>
                            <div className='selected_palette'
                              style={{ ...generateGradientStyle(selectedPaletteColors) }}
                            ></div>

                            <div className="legend_value_container" style={{ width: '100%' }}>

                              <div className="legend_value">
                                {minValueMap}
                              </div>
                              <div className="legend_value">
                                {maxValueMap}
                              </div>

                            </div>
                          </>


                        )}

                        {mapCreator && (
                          <p className='map_summary'><strong>Created By: </strong>{mapCreator}</p>
                        )}


                      </div>


                    </div>

                  </div>
                </div>

              </div>




            </div>
          </div>
        </div>

      ) : (
        <div className='warning_container'>Please Select a map to Export.</div>

      )}

      <div className="page_change_btn">
        <button onClick={handleBack}>&laquo; Back </button>
      </div>


    </div>
  )
}

export default ExportMap