import React, { useState } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Basemap from './SelectMap';
import AddDataPage from './AddDataPage';
import { FaBars, FaTimes } from 'react-icons/fa';
import VisualizeMap from './VisualizeMap';
import ExportMap from './ExportMap';
import HelpPDF from "../assets/Help.pdf"

const Dashboard = ({ setShowAlert, setAlertMessage }) => {
  const [mapData, setMapData] = useState(null);
  const [finalData, setFinalData] = useState([]);

  const [selectedPaletteColors, setSelectedPaletteColors] = useState([]);
  const [selectedPaletteName, setSelectedPaletteName] = useState('');
  const [showExportMap, setShowExportMap] = useState(false);
  const [showVisualizeMap, setShowVisualizeMap] = useState(false);
  const [showSelectMap, setShowSelectMap] = useState(false);
  const [minValueMap, setMinValueMap] = useState([])
  const [maxValueMap, setMaxValueMap] = useState([])
  const [nullColor, setNullColor] = useState('#84857e');

  const [mapCenter, setMapCenter] = useState([0, 0]);
  const [mapZoom, setMapZoom] = useState(5);


  const handleSelectMap = () => {
    setShowSelectMap(false);
    setTimeout(() => {
      setShowSelectMap(true)
    }, 0);

    window.scrollTo(0, 0);
    setShowMenu(false);

  };

  const handleVisualizeMap = () => {
    setShowVisualizeMap(false);
    setTimeout(() => {
      setShowVisualizeMap(true)
    }, 0);

    window.scrollTo(0, 0);
    setShowMenu(false);

  };

  const handleExportMap = () => {
    setShowExportMap(false);
    setTimeout(() => {
      setShowExportMap(true)
    }, 0);

    window.scrollTo(0, 0);
    setShowMenu(false);
  };

  const [showMenu, setShowMenu] = useState(false);
  const handleToggle = () => {
    setShowMenu(!showMenu);
  };
  const handleLinkClick = () => {
    window.scrollTo(0, 0);
    setShowMenu(false);
  };

  const [selectedIndex, setSelectedIndex] = useState(0);
  const totalTabs = 4;


  const handleNext = () => {
    setSelectedIndex(prevIndex => (prevIndex + 1) % totalTabs);
    // setShowSelectMap(false)
    setShowExportMap(false);
    setShowVisualizeMap(false);

    setTimeout(() => {
      // setShowSelectMap(true)
      setShowExportMap(true)
      setShowVisualizeMap(true);
    }, 0);
    window.scrollTo(0, 0);
    setShowMenu(false);

  };

  const handleBack = () => {
    setSelectedIndex(prevIndex => (prevIndex - 1 + totalTabs) % totalTabs);
    // setShowSelectMap(false)
    setShowExportMap(false);
    setShowVisualizeMap(false);

    setTimeout(() => {
      setShowExportMap(true)
      setShowVisualizeMap(true);
      // setShowSelectMap(true)
    }, 0);

    window.scrollTo(0, 0);
    setShowMenu(false);
  };



  return (

    <div >
      <button className="react_tab_toggle" onClick={handleToggle}>
        {showMenu ? <FaTimes /> : <FaBars />}
      </button>

      <Tabs forceRenderTabPanel={true} selectedIndex={selectedIndex} onSelect={index => setSelectedIndex(index)}>

        <TabList className={`tab_menu ${showMenu ? 'show' : ''}`}>
          <Tab onClick={handleSelectMap}>
            1. Select Your Map
          </Tab>
          <Tab onClick={handleLinkClick}>
            2. Add Your Data
          </Tab>

          <Tab onClick={handleVisualizeMap}>
            3. Visualize
          </Tab>
          <Tab onClick={handleExportMap}>
            4. Export Map
          </Tab >

        </TabList>
        <div className="need_help_container">
          <p>Need Help?<a href={HelpPDF} target='_blank' rel="noreferrer"> Learn how to create your first map.</a></p>
        </div>


        <TabPanel>
          <Basemap
            mapData={mapData}
            setMapData={setMapData}
            showSelectMap={showSelectMap}
            setShowSelectMap={setShowSelectMap}
            handleNext={handleNext}
            setMapCenter={setMapCenter}
            setMapZoom={setMapZoom}
            selectedIndex={selectedIndex}

          />
        </TabPanel>

        <TabPanel>
          <AddDataPage
            mapData={mapData}
            finalData={finalData}
            setFinalData={setFinalData}
            setSelectedPaletteColors={setSelectedPaletteColors}
            setSelectedPaletteName={setSelectedPaletteName}
            handleNext={handleNext}
            handleBack={handleBack}
            setShowAlert={setShowAlert}
            setAlertMessage={setAlertMessage}
          />
        </TabPanel>

        <TabPanel>
          <VisualizeMap
            mapData={mapData}
            finalData={finalData}
            showVisualizeMap={showVisualizeMap}
            selectedPaletteColors={selectedPaletteColors}
            setSelectedPaletteColors={setSelectedPaletteColors}
            maxValueMap={maxValueMap}
            setMaxValueMap={setMaxValueMap}
            setMinValueMap={setMinValueMap}
            minValueMap={minValueMap}
            selectedPaletteName={selectedPaletteName}
            setSelectedPaletteName={setSelectedPaletteName}
            handleNext={handleNext}
            handleBack={handleBack}
            nullColor={nullColor}
            setNullColor={setNullColor}
            mapCenter={mapCenter}
            mapZoom={mapZoom}
          />
        </TabPanel>

        <TabPanel>
          <ExportMap
            mapData={mapData}
            finalData={finalData}
            selectedPaletteColors={selectedPaletteColors}
            showExportMap={showExportMap}
            setShowExportMap={setShowExportMap}
            maxValueMap={maxValueMap}
            minValueMap={minValueMap}
            handleBack={handleBack}
            nullColor={nullColor}

          />
        </TabPanel>
      </Tabs>
    </div>


  )
}

export default Dashboard