import React, { useState, useEffect } from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';


const AddDataPage = ({ mapData, finalData, setFinalData, setSelectedPaletteColors, setSelectedPaletteName, handleNext, handleBack,setShowAlert,setAlertMessage }) => {
  const [tableData, setTableData] = useState([]);
  const [searchValue, setSearchValue] = useState('');
  const [filteredData, setFilteredData] = useState([]);




  // Handle value change for a specific item
  const handleValueChange = (index, value) => {
    setTableData((prevData) => {
      const newData = [...prevData];
      const filteredIndex = tableData.indexOf(filteredData[index]);
      newData[filteredIndex] = {
        ...newData[filteredIndex],
        Value: value
      };
      return newData;
    });
  };
  
  // Initialize data on component mount
  useEffect(() => {
    if (mapData && mapData.features) {
      const initialData = mapData.features.map((item) => ({
        Name: item.properties.Name,
        Value: ''
      }));
      setTableData(initialData);
      setFilteredData(initialData); // Initialize filtered data with all table data
    }
  }, [mapData]);

  // Filter the data based on the search value
  useEffect(() => {
    const filtered = tableData.filter((item) => {
      const name = item.Name ? item.Name.toLowerCase() : '';
      const search = searchValue ? searchValue.toLowerCase() : '';
      return name.includes(search);
    });
    setFilteredData(filtered);
  }, [tableData, searchValue]);



  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    setFinalData(tableData);
    setSelectedPaletteColors([])
    setSelectedPaletteName('')
    setAlertMessage("Your Values have been submitted. You can visualize or export the map.")
    setShowAlert(true)
  };


  return (
    <div>
      <form onSubmit={handleSubmit}>

        <div className="add_data_table_search">
          <Autocomplete
            options={tableData.map((item) => item.Name)}
            value={searchValue}
            onChange={(event, newValue) => setSearchValue(newValue)}
            renderInput={(params) => (
              <TextField {...params} label="Search by Name" variant="outlined" />
            )}
          />
        </div>


        {filteredData.length > 0 ? (
          <>
            <div className="add_data_table">
              <table>
                <thead>
                  <tr>
                    <th>Names</th>
                    <th>Values</th>
                  </tr>
                </thead>
                <tbody>
                  {filteredData.map((item, index) => (
                    <tr key={index}>
                      <td>{item.Name}</td>
                      <td>
                        <input
                          type='number'
                          placeholder='Assign Value'
                          value={item.Value}
                          onChange={(e) => handleValueChange(index, e.target.value)}
                        />
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <div>
              <button className='selection_btn_button' type="submit">Submit</button>
            </div>
          </>

        ) : (
          <div className='warning_container'>Please Select a map to add values</div>
        )}


      </form>
      <div className="page_change_btn">
        <button onClick={handleBack}> &laquo; Back </button>
        <button onClick={handleNext}>Proceed &raquo;</button>
      </div>
    </div>


  );
};

export default AddDataPage;