import React, { useState, lazy, Suspense } from "react";
import GlobeMap from "../components/GlobeMap";
const WorldContriesMap = lazy(() => import("../maps/WorldContriesMap"))
const UnitedStatesMap = lazy(() => import("../maps/UnitedStatesMap"))
const AndhraPradeshMap = lazy(() => import("../maps/AndhraPradeshMap"))
const AustraliaMap = lazy(() => import("../maps/AustraliaMap"))
const JapanMap = lazy(() => import("../maps/JapanMap"))
const IndiaMap = lazy(() => import("../maps/IndiaMap"))

const Basemap = ({
    setMapData,
    handleNext,
    setMapCenter,
    setMapZoom,
    showSelectMap,
    setShowSelectMap,
    selectedIndex
}) => {
    const [selectedOption, setSelectedOption] = useState("");


    function setWindowWidth() {
        var viewportWidth = window.innerWidth;
        var windowWidth;
        if (viewportWidth <= [767]) {
          windowWidth = { width: '100%', height: "450px", backgroundColor: 'white', border: 'none', margin: 'auto', borderRadius: "10px", };
        } else {
          windowWidth = { width: '100%', height: "600px", backgroundColor: 'white', border: 'none', margin: 'auto', borderRadius: "10px", };
        }
        return windowWidth;
      }


    const handleOptionChange = (event) => {
        setShowSelectMap(false);
        setMapData(null);
        setSelectedOption(event.target.value);
        setShowSelectMap(true);
    };

    let mapComponent;
    if (selectedOption === "World Countries") {
        mapComponent = (
            <Suspense fallback={<div className='map_loader_container'>
                <div className="preloader">
                    <span className="loader"></span>
                </div>

            </div>}>
                <WorldContriesMap
                    setMapCenter={setMapCenter}
                    setMapZoom={setMapZoom}
                    selectedIndex={selectedIndex}
                    setMapData={setMapData}
                    setWindowWidth={setWindowWidth}
                />
            </Suspense>
        );
    } if (selectedOption === "Andhra Pradesh") {
        mapComponent = (
            <Suspense fallback={<div className='map_loader_container'>
                <div className="preloader">
                    <span className="loader"></span>
                </div>

            </div>}>
                <AndhraPradeshMap
                    setMapCenter={setMapCenter}
                    setMapZoom={setMapZoom}
                    selectedIndex={selectedIndex}
                    setMapData={setMapData}
                    setWindowWidth={setWindowWidth}
                />
            </Suspense>
        );
    }
    if (selectedOption === "Australia") {
        mapComponent = (
            <Suspense fallback={<div className='map_loader_container'>
                <div className="preloader">
                    <span className="loader"></span>
                </div>

            </div>}>
                <AustraliaMap
                    setMapCenter={setMapCenter}
                    setMapZoom={setMapZoom}
                    selectedIndex={selectedIndex}
                    setMapData={setMapData}
                    setWindowWidth={setWindowWidth}
                />
            </Suspense>
        );
    }
    if (selectedOption === "India") {
        mapComponent = (
            <Suspense fallback={<div className='map_loader_container'>
                <div className="preloader">
                    <span className="loader"></span>
                </div>

            </div>}>
                <IndiaMap
                    setMapCenter={setMapCenter}
                    setMapZoom={setMapZoom}
                    selectedIndex={selectedIndex}
                    setMapData={setMapData}
                    setWindowWidth={setWindowWidth}
                />
            </Suspense>
        );
    }
    if (selectedOption === "Japan") {
        mapComponent = (
            <Suspense fallback={<div className='map_loader_container'>
                <div className="preloader">
                    <span className="loader"></span>
                </div>

            </div>}>
                <JapanMap
                    setMapCenter={setMapCenter}
                    setMapZoom={setMapZoom}
                    selectedIndex={selectedIndex}
                    setMapData={setMapData}
                    setWindowWidth={setWindowWidth}
                />
            </Suspense>
        );
    }
    if (selectedOption === "United States") {
        mapComponent = (
            <Suspense fallback={<div className='map_loader_container'>
                <div className="preloader">
                    <span className="loader"></span>
                </div>

            </div>}>
                <UnitedStatesMap
                    setMapCenter={setMapCenter}
                    setMapZoom={setMapZoom}
                    selectedIndex={selectedIndex}
                    setMapData={setMapData}
                    setWindowWidth={setWindowWidth}
                />
            </Suspense>
        );
    }


    

    return (
        <div>
            <div className="row">
                <div className="col-md-3">
                    <div className="select_map_buttons">
                        <div className="section_heading">
                            <h2>Select the map you want to create</h2>
                        </div>

                        <div>


                            <label className="selection_btn">
                                <input
                                    type="radio"
                                    name="shapeFileSelection"
                                    value="Andhra Pradesh"
                                    checked={selectedOption === "Andhra Pradesh"}
                                    onChange={handleOptionChange}
                                />{" "}
                                Andhra Pradesh
                            </label>
                            <label className="selection_btn">
                                <input
                                    type="radio"
                                    name="shapeFileSelection"
                                    value="Australia"
                                    checked={selectedOption === "Australia"}
                                    onChange={handleOptionChange}
                                />{" "}
                                Australia
                            </label>
                            <label className="selection_btn">
                                <input
                                    type="radio"
                                    name="shapeFileSelection"
                                    value="India"
                                    checked={selectedOption === "India"}
                                    onChange={handleOptionChange}
                                />{" "}
                                India
                            </label>
                            <label className="selection_btn">
                                <input
                                    type="radio"
                                    name="shapeFileSelection"
                                    value="Japan"
                                    checked={selectedOption === "Japan"}
                                    onChange={handleOptionChange}
                                />{" "}
                                Japan
                            </label>
                            <label className="selection_btn">
                                <input
                                    type="radio"
                                    name="shapeFileSelection"
                                    value="United States"
                                    checked={selectedOption === "United States"}
                                    onChange={handleOptionChange}
                                />{" "}
                                United States
                            </label>
                            <label className="selection_btn">
                                <input
                                    type="radio"
                                    name="shapeFileSelection"
                                    value="World Countries"
                                    checked={selectedOption === "World Countries"}
                                    onChange={handleOptionChange}
                                />{" "}
                                World Countries
                            </label>

                        </div>

                        <div style={{ width: "100%" }} className="first_page_btn">
                            <button onClick={handleNext}>Proceed &raquo; </button>
                        </div>
                    </div>
                </div>
                <div className="col-md-9">
                    {selectedOption ? (
                        <>
                            {showSelectMap ? (
                                <div className="">{mapComponent}</div>
                            ) : (
                                <div className="map_loader_container">
                                    <div className="preloader">
                                        <div className="loader"></div>
                                    </div>
                                </div>
                            )
                            }
                        </>

                    ) : (
                        <GlobeMap
                            style={{ width: '100%', height: "60vh", backgroundColor: 'whitesmoke' }} />
                    )}

                </div>
            </div>
        </div>
    );
};

export default Basemap;
