import React from 'react';
const ColorPalette = ({ setSelectedPaletteColors, selectedPaletteColors, minValueMap, maxValueMap, selectedPaletteName, setSelectedPaletteName }) => {



    const colorPalettes = [
        { name: 'Palette 1', colors: ['#00FF00', '#FF0000'] },
        { name: 'Palette 2', colors: ['#FFFF00', '#FF00FF'] },
        { name: 'Palette 3', colors: ['#FAF0E4', '#EA906C'] },
        { name: 'Palette 4', colors: ['#D3D04F', '#0E2954'] },

        { name: 'Palette 5', colors: ['#22A699', '#F24C3D'] },
        { name: 'Palette 6', colors: ['#213363', '#D3D04F'] },
        { name: 'Palette 7', colors: ['#B70404', '#FFE569'] },
        { name: 'Palette 8', colors: ['#84857e', '#00FF00'] },
       
    ];

    const handlePaletteChange = (event) => {
        const palette = colorPalettes.find((p) => p.name === event.target.value);
        if (palette) {
            setSelectedPaletteName(palette.name);
            setSelectedPaletteColors(palette.colors)

        }
    };



    const generateGradientStyle = (colors) => {
        if (colors && colors.length >= 2) {
            const gradient = `linear-gradient(to right, ${colors.join(', ')})`;
            return {
                backgroundImage: gradient,
            };
        }
        return null;
    };



    return (
        <div className='color_pallete_container'>
            <div className="dropdown">
                <button
                    className="color_ramp_btn dropdown-toggle"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                >
                    Select Palette
                </button>
                <div className="dropdown-menu">
                    {colorPalettes.map((palette, index) => (
                        <label className='color_ramp_dropdown' key={index} htmlFor={`palette${index}`}>
                            <input
                                type="radio"
                                id={`palette${index}`}
                                name="colorPalette"
                                className='color_ramp_input'
                                value={palette.name}
                                checked={selectedPaletteName === palette.name}
                                onChange={handlePaletteChange}
                            />

                            <div
                                key={index}
                                className="dropdown-item"
                                style={{ ...generateGradientStyle(palette.colors) }}
                                onClick={() => {
                                    setSelectedPaletteName(palette.name);
                                    setSelectedPaletteColors(palette.colors);
                                }}
                            >
                                <p>{palette.name}</p>
                            </div>


                        </label>
                    ))}
                </div>

            </div>
            {selectedPaletteColors.length > 0 && (
                <div>
                    <div className='selected_palette'
                        style={{ ...generateGradientStyle(selectedPaletteColors) }}
                    ></div>
                    <div className="legend_value_container" style={{ width: '100%' }}>
                        <div className="legend_value">
                            {minValueMap}
                        </div>
                        <div className="legend_value">
                            {maxValueMap}
                        </div>

                    </div>

                </div>

            )}


        </div>
    );
};

export default ColorPalette;
